export default {
    data() {
        return {
            titles: [{
                    text: ' ',
                    value: 'product',
                },
                {
                    text: ' ',
                    value: 'general',
                },
                {
                    text: ' ',
                    value: 'delivery',
                },
                {
                    text: '  ',
                    value: 'cancel',
                },
                {
                    text: '  ',
                    value: 'change',
                },
                {
                    text: '  ',
                    value: 'changeAddress',
                },
                {
                    text: ' ',
                    value: 'return',
                },
                {
                    text: ' ',
                    value: 'exchange',
                },
                {
                    text: ' / ',
                    value: 'error',
                },
                {
                    text: '',
                    value: 'generalReply',
                },
                {
                    text: '',
                    value: 'productReply',
                },
                {
                    text: '',
                    value: 'deliveryReply',
                },
                {
                    text: '',
                    value: 'cancelReply',
                },
                {
                    text: '',
                    value: 'changeReply',
                },
                {
                    text: '',
                    value: 'changeaddressReply',
                },
                {
                    text: '',
                    value: 'returnReply',
                },
                {
                    text: '',
                    value: 'exchangeReply',
                },
                {
                    text: '',
                    value: 'errorReply',
                },
            ],
        }
    },
    computed: {
        title() {
            for (let i = 0; i < this.titles.length; i++) {
                if (this.type == this.titles[i].value) {
                    return this.titles[i].text;
                }
            }
            return 'error';
        }
    },
    props: [`type`]
}
