import axios from 'axios'
export default {
    methods: {
        goBack() {
            this.$router.push('/');
        },
        signUp() {
            let validate = this.$refs.form.validate();
            if (!this.check)
                alert("  .")
            else if (validate) {
                let member = {
                    id: this.id,
                    password: this.pwd1,
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                    terms: this.agreement3,
                }
                axios.post('/api/member/insert', member)
                    .then(() => {
                        alert(" ")
                        this.$router.push('/')
                    }).catch(() => {
                        alert(" ")
                    })
            }
        },
        signUpBySocial() {
            let validate = this.$refs.form.validate();
            if (validate) {
                let tempId = null;
                if (this.naverInfo != undefined) {
                    tempId = this.naverInfo.id;
                } else if (this.kakaoInfo != undefined) {
                    tempId = this.kakaoInfo.id;
                }
                let member = {
                    id: tempId,
                    password: tempId + 'rh7369#n',
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                    terms: this.agreement3,
                }
                axios.post('/api/member/insert', member)
                    .then(() => {
                        alert(" ")
                        this.$router.push('/')
                    }).catch(() => {
                        alert(" ")
                    })
            }
        },
        idCheck() {
            axios.get(`/api/member/check/${this.id}`)
                .then(() => {
                    alert(" .")
                    this.check = true;

                })
                .catch(() => {
                    alert(" .")
                    this.check = false;

                })
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.extraAddress !== "") {
                        this.addr2 = "";
                    }
                    if (data.userSelectedType === "R") {
                        //     
                        this.addr1 = data.roadAddress;
                    } else {
                        //     (J)
                        this.addr1 = data.jibunAddress;
                    }

                    // //       .
                    // if (data.userSelectedType === "R") {
                    //     //    . ( )
                    //     //     "//" .
                    //     if (data.bname !== "" && /[||]$/g.test(data.bname)) {
                    //         // this.addr2 += data.bname;
                    //     }
                    //     //  ,   .
                    //     if (data.buildingName !== "" && data.apartment === "Y") {
                    //         this.addr2 +=
                    //             this.addr2 !== "" ?
                    //             `, ${data.buildingName}` :
                    //             data.buildingName;
                    //     }
                    //     //    ,     .
                    //     if (this.addr2 !== "") {
                    //         this.addr2 = `(${this.addr2})`;
                    //     }
                    // } else {
                    //     this.addr2 = "";
                    // }
                    //  .
                    this.zipcode = data.zonecode;
                },
            }).open();
        }
    },
    mounted() {
        this.$vuetify.goTo(0);
        this.naverInfo = this.$route.params.naver;
        this.kakaoInfo = this.$route.params.kakao;
        if (this.naverInfo != undefined) {
            //    
            this.name = this.naverInfo.name;
            this.email = this.naverInfo.email;
        } else if (this.kakaoInfo != undefined) {
            //                
            this.name = this.kakaoInfo.kakao_account.profile.nickname;
        }
    },
    data() {
        return {
            id: '',
            pwd1: '',
            pwd2: '',
            name: '',
            zipcode: '',
            addr1: '',
            addr2: '',
            tel: '',
            email: '',
            agreement1: '',
            agreement2: '',
            agreement3: '',
            naverInfo: '',
            check: false,
            rules: {
                id: [v => !!v || '  .',
                    v => /^[a-zA-Z0-9]*$/.test(v) || ' +  .',
                    v => v.length >= 4 || ' 4.',
                    v => v.length <= 24 || ' 24.',
                ],
                pwd1: [v => !!v || '  .',
                    v => /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^*+=-]).{0,}$/.test(v) || '  , ,  .',
                    v => v.length >= 8 || ' 8.',
                    v => v.length <= 20 || ' 20.',
                ],
                pwd2: [v => !!v || '  .',
                    v => v === this.pwd1 || '  .'
                ],
                name: [v => !!v || '  .', ],
                zipcode: [v => !!v || '  .', ],
                addr1: [v => !!v || '  .', ],
                addr2: [v => !!v || '  .', ],
                tel: [v => !!v || '  .', ],
                email: [v => !!v || '  .',
                    v => /^[A-Za-z0-9_\\.\\-]+@[A-Za-z0-9\\-]+\.[A-Za-z0-9\\-]+/.test(v) || '   .'
                ],
                agreement1: [v => !!v],
                agreement2: [v => !!v],
            },
        }
    },
}
