import axios from 'axios'
import HideId from '@/components/HideId.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        HideId,
    },
    data() {
        //Vue component   , data=key:value
        return {
            dataLoaded: false,
            pageID: '',
            notice: '',
            images: [],
        }
    },
    methods: {
        moveToBefore() {
            this.$router.go(-1);
        },
        moveToUpdate() {
            this.$router.push(`/updatePost/notice/${this.pageID}`)
        },

        //Vue component   , template    
        //Router Vue component   
        getNotice() {
            this.dataLoaded = false;
            axios.get(`/api/notice/list/${this.pageID}`)
                .then((res) => {
                    this.notice = res.data;
                    if (this.notice.image != null) {
                        this.images = this.notice.image.split(';');
                    }
                }).catch((err) => {
                    alert("  .");
                    console.log(err);
                }).finally(
                    this.dataLoaded = true
                )
        },
        deleteNotice() {
            axios.delete(`/api/notice/deleteNotice/${this.pageID}`)
                .then(() => {
                    alert("  ");
                    this.$router.go(-1);
                }).catch((err) => {
                    console.log(err);
                })
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() { //method  DOM <template>     
        this.pageID = this.$route.params.id;
        this.getNotice();
    }
}
