import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
export default {
    components: {
        DateDisplay,
    },
    data() {
        return {
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '',
                value: 'productNo',
                divider: true,
                align: 'center',
                width: '6%',
            }, {
                text: '',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '',
                value: 'type1',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'type2',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '',
                value: 'price',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'discount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'color',
                divider: true,
                align: 'center',
                width: '9%',
            }, {
                text: '',
                value: 'size',
                divider: true,
                align: 'center',
                width: '6%',
            }, {
                text: '',
                value: 'amount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: ' ',
                value: 'regDate',
                divider: true,
                align: 'center',
                width: '7%',
            }, {
                text: ' ',
                value: 'detailImageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: ' ',
                value: 'onSale',
                divider: true,
                align: 'center',
                width: '7%',
            }, {
                text: '',
                value: 'btn',
                align: 'center',
                width: '5%',
            }],

            searches: [{
                text: '',
                value: 'productNo',
            }, {
                text: '',
                value: 'productName',
            }, {
                text: ' ',
                value: 'type',
            }, {
                text: '',
                value: 'price',
            }, {
                text: '',
                value: 'amount',
            }, {
                text: '',
                value: 'regDate',
            }, ],
            search: 'productNo',
            searchWord1: '',
            searchWord2: '',

            products: [],
            colorList: [],
            sizeList: [],

            types: [{
                text: ' ',
                value: null,
            }, {
                text: 'OUTER',
                value: 'outer',
            }, {
                text: 'OUTER>',
                value: 'outer;jacket',
            }, {
                text: 'OUTER>',
                value: 'outer;cardigan',
            }, {
                text: 'OUTER>',
                value: 'outer;jumper',
            }, {
                text: 'OUTER>',
                value: 'outer;coat',
            }, {
                text: 'TOP',
                value: 'top;all',
            }, {
                text: 'TOP>',
                value: 'top;sleeveless',
            }, {
                text: 'TOP>',
                value: 'top;tshirts',
            }, {
                text: 'TOP>',
                value: 'top;knit',
            }, {
                text: 'TOP>',
                value: 'top;shirt',
            }, {
                text: 'TOP>',
                value: 'top;blouse',
            }, {
                text: 'TOP>',
                value: 'top;mtm',
            }, {
                text: 'PANTS',
                value: 'pants;all',
            }, {
                text: 'PANTS>',
                value: 'pants;slacks',
            }, {
                text: 'PANTS>',
                value: 'pants;denim',
            }, {
                text: 'PANTS>',
                value: 'pants;cotton',
            }, {
                text: 'PANTS>SHORT',
                value: 'pants;shorts',
            }, {
                text: 'SKIRT',
                value: 'skirt;all',
            }, {
                text: 'SKIRT>',
                value: 'skirt;mini',
            }, {
                text: 'SKIRT>/',
                value: 'skirt;midi-long',
            }, {
                text: 'DRESS',
                value: 'dress;all',
            }, {
                text: 'DRESS>',
                value: 'dress;ops',
            }, {
                text: 'DRESS>',
                value: 'dress;jumpSuit',
            }, ],
            typeSelected: null,

            menu1: false,
            menu2: false,
        }
    },
    methods: {
        searchProduct() {
            let type1 = null;
            let type2 = null;
            if (this.typeSelected != null) {
                type1 = this.typeSelected.split(';')[0];
                type2 = this.typeSelected.split(';')[1];
            }
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/product/getProductAll`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    type1: type1,
                    type2: type2,
                    search: this.search,
                    searchWord1: this.searchWord1,
                    searchWord2: this.searchWord2,
                }
            }).then(res => {
                this.products = res.data.productList;
                this.totalContents = res.data.count;
            }).catch((err) => {
                this.products = [];
                this.totalContents = 0;
                this.noSearch = true;
                console.log(err);
            })
        },
        reset() {
            this.searchWord1 = null;
            this.searchWord2 = null;
            this.typeSelected = null;
            if (this.search == 'price') {
                this.searchWord1 = 0;
                this.searchWord2 = 9999999;
            } else if (this.search == 'amount') {
                this.searchWord1 = 0;
                this.searchWord2 = 9999;
            } else if (this.search == 'regDate') {
                let date = new Date();
                this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.searchProduct();
        },
        changeOnSale(item) {
            axios.patch(`/api/product/updateOnSale/${item.productNo}`)
                .then(() => {
                    alert('  ');
                    this.searchProduct();
                }).catch(err => {
                    alert(' .')
                    console.log(err);
                })
        },
        searchPolicy() {
            if (this.search == 'price') {
                if (this.searchWord1 < 0 || this.searchWord1 > 9999999 || this.searchWord1 != Math.round(this.searchWord1)) {
                    alert('  : 0 ~ 9,999,999');
                    this.searchWord1 = 0;
                } else if (this.searchWord2 < 0 || this.searchWord2 > 9999999 || this.searchWord2 != Math.round(this.searchWord2)) {
                    alert('  : 0 ~ 9,999,999');
                    this.searchWord2 = 9999999;
                }
            } else {
                if (this.searchWord1 < 0 || this.searchWord1 > 9999 || this.searchWord1 != Math.round(this.searchWord1)) {
                    alert('  : 0 ~ 9,999');
                    this.searchWord1 = 0;
                } else if (this.searchWord2 < 0 || this.searchWord2 > 9999 || this.searchWord2 != Math.round(this.searchWord2)) {
                    alert('  : 0 ~ 9,999');
                    this.searchWord2 = 9999;
                }
            }
        },
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
    },
    watch: { //            
        options: {
            handler() {
                this.searchProduct();
            },
            deep: true,
        },
        search: {
            handler() {
                this.searchWord1 = '';
                this.searchWord2 = '';
                this.typeSelected = null;
                if (this.search == 'price') {
                    this.searchWord1 = 0;
                    this.searchWord2 = 9999999;
                } else if (this.search == 'amount') {
                    this.searchWord1 = 0;
                    this.searchWord2 = 9999;
                } else if (this.search == 'regDate') {
                    let date = new Date();
                    this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                    this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
            }
        },

    },
}
