                scroll = document.querySelector("#orderSelected").offsetTop;
            } else if (tag == 'pay') {
                scroll = document.querySelector("#paySelected").offsetTop;
            } else if (tag == 'delivery') {
                scroll = document.querySelector("#deliverySelected").offsetTop;
