                scroll = document.querySelector("#exchangeSelected").offsetTop;
            } else if (tag == 'return') {
                scroll = document.querySelector("#returnSelected").offsetTop;
            } else if (tag == 'else') {
                scroll = document.querySelector("#elseSelected").offsetTop;
            }
            window.scrollTo({
                top: scroll,
            });
        },
