import axios from 'axios'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import ProductDetailDisplay from '@/components/ProductDetailDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        ProductDetailDisplay,
    },
    data() {
        return {
            editor: ClassicEditor,
            editorConfig: {
                ckfinder: {},
            },
            pageID: '',
            num: '',
            originalNo: '',
            productNo: '',
            admin: false,
            titles: [{
                text: ' ',
                value: 'default',
                content: '',
                type: 'all',
                disabled: true,
            }, {
                text: ' ',
                value: 'product',
                content: '<p>   .<br><br>&#8251;          .<br><br>---------------------------------------------<br></p>',
                type: 'product',
            }, {
                text: ' ',
                value: 'product',
                content: '<p>   .<br><br>&#8251;          .<br><br>---------------------------------------------<br></p>',
                type: 'productQnA',
            }, {
                text: ' ',
                value: 'general',
                content: '<p>   .<br>     !<br><br>&#8251;          .<br><br>---------------------------------------------<br></p>',
                type: 'productQnA',
            }, {
                text: ' ',
                value: 'delivery',
                content: '<p>  ///  <br>!<strong> &#39; /&#39; </strong> !<br><br>---------------------------------------------<br>:</p>',
                type: 'deliveryQnA',
            }, {
                text: '  ',
                value: 'cancel',
                content: "<p><br>    <br>/   .<br>     / .<br>      :)<br><br>-----------------------------------------------<br>**(   &#39;&#39; ! !)<br><br><strong> :</strong><br><strong>():</strong><br><strong>   </strong><br><strong>:</strong><br><strong> :</strong><br><strong> :</strong><br><br>      </p>",
                type: 'beforeDeliveryQnA',
            }, {
                text: '  ',
                value: 'change',
                content: '<p>      <br><br>    <br>/   .<br>     / .<br>      :)<br><br>-----------------------------------------------<br>**<br><br> :<br>  (,) :<br>  (,) :</p>',
                type: 'beforeDeliveryQnA',
            }, {
                text: '  ',
                value: 'changeAddress',
                content: '<p>      <br><br><br>-----------------------------------------------<br>* *<br><br> :<br> () :</p>',
                type: 'beforeDeliveryQnA',
            }, {
                text: ' ',
                value: 'return',
                content: '<p>      <br><br><br>    <br>/   .<br>     / .<br>      :)<br><br>--------------------------------------<br>**<br><br><strong> :</strong><br><strong>(,):</strong><br><strong>  :</strong><br><strong>&#8251;   &#8251;</strong><br><strong>() :</strong><br><strong> :</strong><br><br>(  )<br>&#8251;       .<br><br>:<br><br>--------------------------------------<br>&nbsp;</p>',
                type: 'afterDeliveryQnA',
            }, {
                text: ' ',
                value: 'exchange',
                content: '<p>      <br><br><br>    <br>/   .<br>     / .<br>      :)<br><br>--------------------------------------<br>**<br><br><strong> :</strong><br><strong> (,) :</strong><br><strong> (,) :</strong><br><strong>  / :</strong><br><br>(  )<br>/       .<br><br>:<br>  :<br>&nbsp;</p>',
                type: 'afterDeliveryQnA',
            }, {
                text: ' / ',
                value: 'error',
                content: '<p>      <br><br>--------------------------------------<br>*/*<br><br><strong>()</strong><br><strong></strong><br><strong>( !)</strong><br><br> :<br>/ (  !) :<br>  : ( / )<br>  : ( / )<br>(,) :<br>/  :<br>( ) :<br><br>(  / .)</p>',
                type: 'afterDeliveryQnA',
            }, ],
            faqType: [{
                text: ' ',
                value: 'product',
            }, {
                text: ' ',
                value: 'delivery',
            }, {
                text: '/ ',
                value: 'return',
            }, {
                text: ' ',
                value: 'etc',
            }, ],
            titleSelected: 'default',
            titleDetail: '',
            faqTypeSelected: '',
            star: 0,
            content: '',
            contentColor: 'black--text',
            imageFiles: [null],
            imageUrl: [null],
            secret: true,
            id: '',
        }
    },
    methods: {
        //    
        currentURL() {
            if (this.pageID == 'notice' || this.pageID == 'faq') {
                this.admin = true;
                this.titleSelected = this.pageID;
            } else {
                for (let i = this.titles.length - 1; i > 0; i--) {
                    if (this.pageID != this.titles[i].type) {
                        this.titles.splice(i, 1);
                    }
                }
            }
            for (let j = this.imageFiles.length; j < 4; j++) {
                this.imageFiles.push(null);
            }
        },

        //       
        setContent(target) {
            for (let i = 0; i < this.titles.length; i++) {
                if (target == this.titles[i].value) {
                    this.content = this.titles[i].content;
                }
            }
        },

        // 
        fileInputClick(idx) {
            document.getElementById(`fileInput${idx}`).click();
        },
        onImageChange(index) {
            const file = this.imageFiles[index];
            if (file) {
                this.imageUrl[index] = URL.createObjectURL(file);
                URL.revokeObjectURL(file);
            } else {
                this.imageUrl[index] = null;
            }
        },

        // 
        moveToBefore() {
            this.$router.go(-1);
        },

        form() {
            if (this.admin) {
                if (this.titleDetail == '' && this.originalNo == undefined) {
                    alert(' ');
                    return;
                }
            } else {
                if (this.titleSelected == 'default') {
                    alert(' ')
                    return;
                }
            }

            if (this.content == '') {
                alert(' ');
                return;
            }

            let limit = 2000;
            if (this.pageID == 'notice') {
                limit = 10000;
            }
            if (this.content.length > limit) {
                alert('   ');
                return;
            }

            if (this.originalNo != undefined) {
                this.replyForm();
            } else if (this.pageID == 'notice') {
                this.noticeForm();
            } else if (this.pageID == 'faq') {
                this.faqForm();
            } else {
                this.qnaForm();
            }
        },
        getData() {
            this.titleSelected = this.pageID;
            if (this.pageID == 'notice') {
                this.admin = true;
                this.getNotice();
            } else if (this.pageID == 'review') {
                this.getReview();
            } else if (this.pageID == 'faq') {
                this.admin = true;
                this.getFAQ();
            } else if (this.pageID == 'qna') {
                this.getQnA();
            }
        },
        updateForm() {
            if (this.admin) {
                if (this.titleDetail == '') {
                    alert(' ');
                    return;
                }
            } else {
                if (this.titleSelected == 'default') {
                    alert(' ')
                    return;
                }
            }

            if (this.content == '') {
                alert(' ');
                return;
            }

            let limit = 2000;
            if (this.pageID == 'notice') {
                limit = 10000;
            }
            if (this.content.length > limit) {
                alert('   ');
                return;
            }

            if (this.pageID == 'notice') {
                this.noticeFormUpdate();
            } else if (this.pageID == 'review') {
                this.reviewFormUpdate();
            } else if (this.pageID == 'faq') {
                this.faqFormUpdate();
            } else {
                this.qnaFormUpdate();
            }
        },

        //  
        replyForm() {
            axios({
                method: 'post',
                url: `/api/qna/insertReply`,
                data: {
                    type: this.pageID + 'Reply',
                    originalNo: this.originalNo,
                    content: this.content,
                    id: this.id,
                }
            }).then(() => {
                alert("  ");
                this.$router.go(-2);
            }).catch((err) => {
                console.log(err);
            })
        },

        // 
        noticeForm() {
            let image = null;
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    if (image == null) {
                        image = this.imageFiles[i].name;
                    } else {
                        image = image + ";" + this.imageFiles[i].name;
                    }
                }
            }
            let data = {
                title: this.titleDetail,
                content: this.content,
                id: this.id,
                image: image,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    formData.append(`fileList`, this.imageFiles[i])
                }
            }
            axios.post(`/api/notice/insertNotice`, formData)
                .then(() => {
                    alert("  ");
                    this.$router.go(-1);
                }).catch((err) => {
                    alert(" ");
                    console.log(err);
                })
        },
        faqForm() {
            axios.post('/api/faq/insertfaq', {
                    type: this.faqTypeSelected,
                    title: this.titleDetail,
                    content: this.content
                })

                .then(() => {
                    alert("FAQ  ");
                    this.$router.go(-1);
                }).catch((err) => {
                    console.log(err);
                })

        },
        qnaForm() {
            let image = null;
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    if (image == null) {
                        image = this.imageFiles[i].name;
                    } else {
                        image = image + ";" + this.imageFiles[i].name;
                    }
                }
            }
            let data = {
                productNo: this.productNo,
                type: this.titleSelected,
                reply: false,
                content: this.content,
                id: this.id,
                secret: this.secret,
                image: image,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    formData.append(`fileList`, this.imageFiles[i])
                }
            }
            axios.post(`/api/qna/insertqna`, formData)
                .then(() => {
                    alert(" .");
                    this.$router.go(-1);
                }).catch((err) => {
                    console.log(err);
                })
        },
        //     
        getNotice() {
            axios.get(`/api/notice/list/${this.num}`)
                .then((res) => {
                    this.titleDetail = res.data.title;
                    this.content = res.data.content;
                    let imageList = null;
                    if (res.data.image != null) {
                        imageList = res.data.image.split(';');
                        for (let i = 0; i < imageList.length; i++) {
                            axios.get(`/api/notice/noticeImage/${this.num}/${imageList[i]}`, {
                                    responseType: "blob",
                                })
                                .then(res => {
                                    if (i == 0) {
                                        this.imageFiles.pop();
                                    }
                                    var file = new File([res.data], imageList[i], {
                                        type: "image/*",
                                        lastModified: Date.now()
                                    });
                                    this.imageFiles.push(file);
                                    this.onImageChange(i);

                                    if (i == imageList.length - 1) {
                                        for (let j = i + 1; j < 4; j++) {
                                            this.imageFiles.push(null);
                                        }
                                    }
                                })
                        }
                    }
                }).catch((err) => {
                    alert("  .");
                    console.log(err);
                })
        },
        getReview() {
            axios.get(`/api/review/getReview/${this.num}`)
                .then((res) => {
                    this.content = res.data.content;
                    this.star = res.data.star;
                    axios.get(`/api/review/reviewImage/${this.num}/${res.data.image}`, {
                            responseType: "blob",
                        })
                        .then(res => {
                            this.imageFiles.pop();
                            let file = new File([res.data], res.data.image, {
                                type: "image/*",
                                lastModified: Date.now()
                            });
                            this.imageFiles.push(file);
                            this.onImageChange(0);
                        })
                }).catch((err) => {
                    alert("  .");
                    console.log(err);
                })
        },
        getFAQ() {
            axios.get(`/api/faq/getFaq/${this.num}`)
                .then((res) => {
                    this.titleDetail = res.data.title;
                    this.faqTypeSelected = res.data.type;
                    this.content = res.data.content;
                }).catch((err) => {
                    alert("  .");
                    console.log(err);
                })
        },
        getQnA() {
            axios.get(`/api/qna/getQna/${this.num}`)
                .then((res) => {
                    this.titleSelected = res.data.type;
                    this.content = res.data.content;
                    this.secret = res.data.secret;
                    if (res.data.image != null) {
                        let imageList = res.data.image.split(';');
                        for (let i = 0; i < imageList.length; i++) {
                            axios.get(`/api/qna/qnaImage/${this.num}/${imageList[i]}`, {
                                    responseType: "blob",
                                })
                                .then(res => {
                                    if (i == 0) {
                                        this.imageFiles.pop();
                                    }
                                    var file = new File([res.data], imageList[i], {
                                        type: "image/*",
                                        lastModified: Date.now()
                                    });
                                    this.imageFiles.push(file);
                                    this.onImageChange(i);

                                    if (i == imageList.length - 1) {
                                        for (let j = i + 1; j < 4; j++) {
                                            this.imageFiles.push(null);
                                        }
                                    }
                                })
                        }
                    }
                }).catch((err) => {
                    alert("  .");
                    console.log(err);
                })
        },

        // 
        noticeFormUpdate() {
            let image = null;
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    if (image == null) {
                        image = this.imageFiles[i].name;
                    } else {
                        image = image + ";" + this.imageFiles[i].name;
                    }
                }
            }
            let data = {
                noticeNo: this.num,
                title: this.titleDetail,
                content: this.content,
                image: image,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    formData.append(`fileList`, this.imageFiles[i])
                }
            }
            axios.patch(`/api/notice/updateNotice`, formData)
                .then(() => {
                    alert("  ");
                    this.$router.go(-1);
                }).catch((err) => {
                    alert(" ");
                    console.log(err);
                })
        },
        reviewFormUpdate() {
            let image = null;
            if (this.imageFiles[0] != null) {
                image = this.imageFiles[0].name;
            }
            let data = {
                reviewNo: this.num,
                content: this.content,
                star: this.star,
                image: image,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));

            formData.append(`fileList`, this.imageFiles[0]);
            axios.patch(`/api/review/update`, formData)
                .then(() => {
                    alert(" .")
                    this.$router.go(-1);
                }).catch((err) => {
                    alert(' .');
                    console.log(err);
                })
        },
        faqFormUpdate() {
            axios({
                method: 'patch',
                url: `/api/faq/updatefaq`,
                params: {
                    faqNo: this.num,
                    type: this.faqTypeSelected,
                    title: this.titleDetail,
                    content: this.content,
                }
            }).then(() => {
                alert("FAQ  ");
                this.$router.go(-1);
            })
        },
        qnaFormUpdate() {
            let image = null;
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    if (image == null) {
                        image = this.imageFiles[i].name;
                    } else {
                        image = image + ";" + this.imageFiles[i].name;
                    }
                }
            }
            let data = {
                productNo: this.productNo,
                qnaNo: this.num,
                type: this.titleSelected,
                content: this.content,
                secret: this.secret,
                image: image,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            for (let i = 0; i < this.imageFiles.length; i++) {
                if (this.imageFiles[i] != null) {
                    formData.append(`fileList`, this.imageFiles[i])
                }
            }
            axios.patch(`/api/qna/updateqna`, formData)
                .then(() => {
                    alert(" .");
                    this.$router.go(-1);
                }).catch((err) => {
                    console.log(err);
                    alert(" .");
                })
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        content: {
            handler() {
                let limit = 2000;
                if (this.pageID == 'notice') {
                    limit = 10000;
                }
                // 2000   ( 1)
                if (this.content.length > limit) {
                    this.contentColor = 'red--text';
                } else {
                    this.contentColor = 'black--text';
                }
            }
        },
    },
    mounted() {
        this.id = this.getLogin.user.id;
        // notice, review, faq, qna
        this.pageID = this.$route.params.id;
        //   
        this.num = this.$route.params.num;
        //   
        this.originalNo = this.$route.params.original;
        //        
        this.productNo = this.$route.params.productNo;

        if (this.$route.params.num != undefined) {
            // 
            this.getData();
        } else if (this.$route.params.original != undefined) {
            // 
            this.admin = true;
        } else {
            //   
            this.currentURL();
        }
    }
}
