import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
export default {
    components: {
        DateDisplay,
    },
    data() {
        return {
            orders: [],
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '',
                value: 'orderIdx',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'productNo',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '',
                value: 'totalPrice',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '',
                value: 'orderAmount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: ' ',
                value: 'orderDate',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '()',
                value: 'id',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: ' ',
                value: 'state',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: ' ',
                value: 'stateChange',
                align: 'center',
                width: '10%',
            }, ],

            searches: [{
                text: '',
                value: 'orderIdx',
            }, {
                text: '',
                value: 'productNo',
            }, {
                text: '',
                value: 'productName',
            }, {
                text: ' ',
                value: 'orderDate',
            }, {
                text: ' ',
                value: 'state',
            }, ],
            search: 'orderIdx',
            searchWord1: '',
            searchWord2: '',

            states: [{
                text: ' ',
                value: null,
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, {
                text: '',
                value: '',
            }, ],
            stateSelected: null,

            stateChanges: [],

            menu1: false,
            menu2: false,

            dialog: false,
            memberInfo: {},
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        searchOrder() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/order/getOrder`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    state: this.stateSelected,
                    search: this.search,
                    searchWord1: this.searchWord1,
                    searchWord2: this.searchWord2,
                }
            }).then(res => {
                this.orders = res.data.orderList;
                this.totalContents = res.data.count;
                for (let i = 0; i < this.orders.length; i++) {
                    this.stateChanges.push(this.orders[i].state);
                }
            }).catch((err) => {
                this.orders = [];
                this.totalContents = 0;
                console.log(err);
            }).finally(
                this.loading = false
            )
        },
        reset() {
            this.searchWord1 = null;
            this.searchWord2 = null;
            this.stateSelected = null;
            if (this.search == 'orderDate') {
                let date = new Date();
                this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.searchOrder();
        },
        selectItem(item) {
            this.memberInfo = {
                id: item.id,
                name: item.name,
                tel: item.tel,
                zipCode: item.zipCode,
                address: item.address,
                detailAddr: item.detailAddr,
            }
        },
        stateChange() {
            let states = [];
            for (let i = 0; i < this.orders.length; i++) {
                if (this.orders[i].state != this.stateChanges[i]) {
                    if (this.stateChanges[i] == null) {
                        alert('   .');
                        return;
                    }
                    if (this.orders[i].state == '' || this.orders[i].state == '' || this.orders[i].state == '') {
                        alert(` : ${this.orders[i].orderIdx}\n ,  ,     .`);
                        continue;
                    }
                    let data = {
                        orderIdx: this.orders[i].orderIdx,
                        state: this.stateChanges[i],
                    }
                    states.push(data);
                }
            }
            if (states.length == 0) {
                alert('  ');
                return;
            }
            axios.patch(`/api/order/update`, states)
                .then(res => {
                    if (res.data.length == 0) {
                        alert('');
                        this.searchOrder();
                    } else {
                        alert(` ( ${res.data.length})\n : ${res.data}`)
                    }
                }).catch(err => {
                    console.log(err)
                })

        },
    },
    watch: { //            
        options: {
            handler() {
                this.searchOrder();
            },
            deep: true,
        },
        search: {
            handler() {
                this.searchWord1 = '';
                this.searchWord2 = '';
                this.stateSelected = null;
                if (this.search == 'orderDate') {
                    let date = new Date();
                    this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                    this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
            }
        },

    },
}
